import service from "./request"

/**用户注册 及登录*/
export const insertUser = (params) =>
    service.post({
        url: "/user/insertUser",
        data: params
    })

/**获取近3月的活跃用户*/
export const getActiveUser = () =>
    service.get({
        url: "/user/getActiveUser"
    })

/**用户累计注册总数：用户当月注册总数*/
export const getCountRegUser = () =>
    service.get({
        url: "/user/getCountRegUser"
    })